@use "reset";
@use "fonts";
@use "vars";
@use "utils";
@use "typography";
@use "header";
@use "hero";
@use "accordion";

@use 'vars' as *;

.container {
  padding: 100px 0;
  max-width: 96%;
  margin: 0 auto;
  &-sml {
    max-width: 900px;
  }
  &-md {
    max-width: 1300;
  }
}

.schedule {
  max-width: 500px;
  margin: 0 auto;
  &_heading {
    margin: 20px 0 50px 0;
  }


}

.countdown-timer {
  display: flex;
  justify-content: center;
  .countdown-item {
    display: flex;
    letter-spacing: 4px;
    font-size: 12px;
    margin-right: 10px;
  }
  &.footer-timing {
    margin-top: 20px;
    .countdown-item {
      font-size: 18px;
    }
    .countdown-label {
      font-size: 18px;
      letter-spacing: 4px;
    }
  }
    .days, .hours, .minutes, .seconds {
      font-weight: 400;
    } 
  .countdown-label {
    letter-spacing: 4px;
    font-size: 12px;
    text-transform: uppercase;
  }
}

.tab-headers {
  .tab-header {
    opacity: 0.5;
    &:hover {
      cursor: pointer;
    }
    h4 {
      display: inline-block;
    }
    &.active {
      opacity: 1;
      h4 {
        border-bottom: 1px dashed;
      }
    }
    &:nth-of-type(1) {
      margin-bottom: 20px;
    }
  }
}

.tab-content-container {
  position: relative;
  
  .tab-content {
    opacity: 0;
    visibility: hidden;
    height: 0;
    overflow: hidden;
    transition: opacity 300ms;
    
    &.active {
      opacity: 1;
      visibility: visible;
      height: auto;
      overflow: visible;
    }
  }
}

.accomo {
  display: flex;
  .accomo-1 {
    width: 50%;
  }
  .accomo-2 {
    width: 50%;
  }
}

table {
  margin-top: 30px;
  width: 100%;
  border-collapse: separate; // Changed from collapse to allow spacing
  border-spacing: 0 15px;
  tr {
    position: relative;
    border-bottom: none; // Remove the standard border
    margin-bottom: 15px;
    
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      border-bottom: 2px dotted $color-secondary;
      opacity: 0.6; // Makes it a bit more subtle
    }
    td {
      padding-bottom: 10px;
      &:first-child {
        text-align: left;
        font-weight: 400;
      }
      &:last-child {
        text-align: right;
      }
    }
  }
  &.skolly {
    tr {
      td {
        &:first-child {
          width: 100px;
        }
      }
    }
  }
}